import React, { Component } from 'react';

import {startGame, stopGame} from '../lib/Tetris';

import './TetrisComp.css';

export default class TetrisComp extends Component {

  canvasRef = null;

  handleRef = (ref) => {
    if (ref) {
      this.canvasRef = ref;
      startGame( this.canvasRef.getContext('2d') );
    } else {
      stopGame();
    }
  }

  render() {
    return <section className='topic-body'>
      <h4 className='topic-title'>俄罗斯方块</h4>
      <canvas className='tetris' width={200} height={320} ref={this.handleRef}></canvas>
    </section>;
  }
}


