import React, { Component } from 'react';

import { BrowserRouter as Router, Route } from 'react-router-dom';
import CompLoader from './comm/DynamicComp';
import Home from './parts/HomeComp';
import TetrisComp from './parts/TetrisComp';

import logo from './logo.svg';
import './App.css';

class App extends Component {
  render() {

    return (
      <div className='App'>
        <header className='App-header'>
          <img src={logo} className='App-logo' alt='logo' />
          <h1 className='App-title'>Welcome to my playground.</h1>
        </header>

        <Router>
          <div>
            <Route exact path='/' component={Home} />
            <Route path='/qt' component={CompLoader(() => import('./parts/QuadTreeComp'))} />
            {/* <Route path='/trs' component={CompLoader(() => import('./parts/TetrisComp'))} /> */}
            <Route path='/trs' component={TetrisComp} />
          </div>
        </Router>
      </div>
    );
  }
}

export default App;



