import React from 'react';
import Loadable from 'react-loadable';

function LoadingComponent(props) {
    if (props.error) {
        return <div>Error!</div>;
    } else {
        return <div>Loading...</div>;
    }
}

/**
 * {function} loader
 */
export default (loader) => {
    return Loadable({
        loader,
        loading: LoadingComponent,
    })
}



